"use client";
import { useState, useEffect } from 'react';
import { useFinance } from '@/context/FinanceContext';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Label } from '@/components/ui/label';
import { Switch } from '@/components/ui/switch';
import { Tabs, TabsList, TabsTrigger } from '@/components/ui/tabs';
import { Checkbox } from '@/components/ui/checkbox';
import { Save, ArrowRightLeft, CalendarClock, AlertTriangle } from 'lucide-react';

interface Props {
  onClose?: () => void;
  defaultType?: 'income' | 'expense' | 'transfer';
}

export default function TransactionForm({ onClose, defaultType = 'expense' }: Props) {
  const { addTransaction, accounts, categories, fixedRules } = useFinance(); // <--- Traemos fixedRules
  
  const [mode, setMode] = useState<'income' | 'expense' | 'transfer'>(defaultType);
  
  // Estados Generales
  const [amount, setAmount] = useState('');
  const [desc, setDesc] = useState('');
  const [date, setDate] = useState(new Date().toISOString().split('T')[0]);
  const [accountId, setAccountId] = useState('');
  const [categoryId, setCategoryId] = useState('');

  // Lógica Recurrente / Fija
  const [isFixedMode, setIsFixedMode] = useState(false); // ¿Estamos en modo recurrente?
  const [selectedRuleId, setSelectedRuleId] = useState(''); // ID de la regla seleccionada
  const [daysLate, setDaysLate] = useState<number | null>(null); // Días de retraso

  // Lógica Crear Nueva Regla
  const [createRuleFromTx, setCreateRuleFromTx] = useState(false);
  const [newRuleDay, setNewRuleDay] = useState<string>('1');

  // Estados Lógica Ahorro y Transferencia (Existentes)
  const [isSavings, setIsSavings] = useState(false);
  const [didWithdraw, setDidWithdraw] = useState(true);
  const [targetAccountId, setTargetAccountId] = useState('');

  // --- EFECTO: AL SELECCIONAR UNA REGLA EXISTENTE ---
  useEffect(() => {
    if (isFixedMode && selectedRuleId) {
        const rule = fixedRules.find(r => r.id === selectedRuleId);
        if (rule) {
            setAmount(rule.budgetedAmount.toString());
            setDesc(rule.description);
            setCategoryId(rule.categoryId);
            setAccountId(rule.accountIdToCharge);
        }
    }
  }, [selectedRuleId, isFixedMode, fixedRules]);

  // --- EFECTO: CALCULAR RETRASO (Al cambiar fecha o regla) ---
  useEffect(() => {
    if (isFixedMode && selectedRuleId) {
        const rule = fixedRules.find(r => r.id === selectedRuleId);
        if (rule) {
            // Extraemos el día de la fecha seleccionada (formato YYYY-MM-DD)
            // Usamos split para evitar problemas de zona horaria con new Date()
            const selectedDay = parseInt(date.split('-')[2]);
            const ruleDay = rule.dayOfMonth;

            if (selectedDay > ruleDay) {
                setDaysLate(selectedDay - ruleDay);
            } else {
                setDaysLate(null);
            }
        }
    } else {
        setDaysLate(null);
    }
  }, [date, selectedRuleId, isFixedMode, fixedRules]);


  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    if (!amount || !accountId) return;

    const numAmount = parseFloat(amount);
    const safeDate = new Date(`${date}T12:00:00`).toISOString();

    if (mode === 'transfer') {
        // ... (Lógica de transferencia igual) ...
        if (!targetAccountId) return;
        const originAcc = accounts.find(a => a.id === accountId);
        const targetAcc = accounts.find(a => a.id === targetAccountId);
        addTransaction({
            type: 'transfer', amount: numAmount, accountId, relatedAccountId: targetAccountId,
            originAccountName: originAcc?.name, destAccountName: targetAcc?.name,
            date: safeDate, description: desc || 'Transferencia'
        });
    } else {
        // Gasto o Ingreso
        addTransaction({
            type: mode,
            amount: numAmount,
            accountId,
            categoryId,
            description: desc,
            date: safeDate,
            isFixed: isFixedMode, // Marcamos la transacción como fija
            isSavings: mode === 'expense' && isSavings,
            didWithdraw: mode === 'expense' && isSavings ? didWithdraw : true,
            // Datos para crear nueva regla
            createFixedRule: createRuleFromTx,
            dayOfMonth: parseInt(newRuleDay)
        });
    }
    
    if (onClose) onClose();
  };

  const filteredCategories = categories.filter(c => mode === 'income' ? c.type === 'income' : c.type !== 'income');

  return (
    <div className="space-y-4 text-white">
      <Tabs value={mode} onValueChange={(v:any) => { setMode(v); setIsSavings(false); setIsFixedMode(false); }} className="w-full">
        <TabsList className="grid w-full grid-cols-3 bg-zinc-800">
          <TabsTrigger value="income" className="data-[state=active]:bg-green-600">Ingreso</TabsTrigger>
          <TabsTrigger value="expense" className="data-[state=active]:bg-red-600">Gasto</TabsTrigger>
          <TabsTrigger value="transfer" className="data-[state=active]:bg-blue-600">Transferir</TabsTrigger>
        </TabsList>
      </Tabs>

      <form onSubmit={handleSubmit} className="space-y-4 mt-4">
        
        {/* --- SECCIÓN RECURRENTE (Solo Gasto) --- */}
        {mode === 'expense' && (
            <div className="bg-zinc-900/50 p-3 rounded-lg border border-zinc-800 space-y-3">
                <div className="flex items-center justify-between">
                    <div className="flex items-center gap-2">
                        <CalendarClock className="h-4 w-4 text-indigo-400"/>
                        <Label className="cursor-pointer">¿Es un Gasto Recurrente?</Label>
                    </div>
                    <Switch checked={isFixedMode} onCheckedChange={setIsFixedMode} className="data-[state=checked]:bg-indigo-500"/>
                </div>

                {/* Si es recurrente, mostramos selector de reglas */}
                {isFixedMode && (
                    <div className="animate-in slide-in-from-top-2 pt-2 border-t border-zinc-800">
                        <Label className="text-xs text-zinc-400 mb-1 block">Seleccionar regla existente</Label>
                        <Select onValueChange={setSelectedRuleId} value={selectedRuleId}>
                            <SelectTrigger className="bg-zinc-950 border-zinc-700">
                                <SelectValue placeholder="-- Selecciona pago --" />
                            </SelectTrigger>
                            <SelectContent>
                                {fixedRules.map(rule => (
                                    <SelectItem key={rule.id} value={rule.id}>
                                        {rule.description} (Día {rule.dayOfMonth})
                                    </SelectItem>
                                ))}
                            </SelectContent>
                        </Select>

                        {/* --- ALERTA DE RETRASO --- */}
                        {daysLate !== null && (
                            <div className="mt-3 bg-red-900/20 border border-red-500/50 p-3 rounded flex items-start gap-3">
                                <AlertTriangle className="h-5 w-5 text-red-500 shrink-0 mt-0.5" />
                                <div>
                                    <p className="text-sm font-bold text-red-400">¡Pago Retrasado!</p>
                                    <p className="text-xs text-red-200">
                                        Este pago es el día {fixedRules.find(r=>r.id===selectedRuleId)?.dayOfMonth}. 
                                        Llevas <span className="font-bold underline">{daysLate} días</span> de retraso.
                                        ¡Haz el pago ahora!
                                    </p>
                                </div>
                            </div>
                        )}
                    </div>
                )}
            </div>
        )}

        <div className="space-y-1">
            <Label>Fecha del pago</Label>
            <Input type="date" value={date} onChange={e => setDate(e.target.value)} className="bg-black border-zinc-700" />
        </div>

        {/* ... (Lógica de Ahorro existente se mantiene igual) ... */}
        {/* ... (Input Monto se mantiene igual) ... */}
        <div className="space-y-2">
            <Label className="text-xl font-bold">Monto</Label>
            <Input 
                type="number" step="0.01" placeholder="$0" required 
                className="text-2xl font-bold bg-black border-zinc-700 h-14"
                value={amount} onChange={e => setAmount(e.target.value)}
            />
        </div>

        {/* --- SELECTORES DE CUENTA Y CATEGORÍA --- */}
        {mode !== 'transfer' && (
             <div className="grid grid-cols-2 gap-4">
                <div className="space-y-2">
                   <Label>Cuenta</Label>
                   <Select onValueChange={setAccountId} value={accountId} required>
                       <SelectTrigger className="bg-zinc-800 border-zinc-700"><SelectValue placeholder="Seleccionar" /></SelectTrigger>
                       <SelectContent>
                           {accounts.filter(a => !a.isHidden).map(acc => (
                               <SelectItem key={acc.id} value={acc.id}>{acc.name}</SelectItem>
                           ))}
                       </SelectContent>
                   </Select>
                </div>
                <div className="space-y-2">
                   <Label>Categoría</Label>
                   <Select onValueChange={setCategoryId} value={categoryId} required>
                       <SelectTrigger className="bg-zinc-800 border-zinc-700"><SelectValue placeholder="Seleccionar" /></SelectTrigger>
                       <SelectContent>
                           {filteredCategories.map(cat => (
                               <SelectItem key={cat.id} value={cat.id}>{cat.name}</SelectItem>
                           ))}
                       </SelectContent>
                   </Select>
                </div>
            </div>
        )}
        
        {/* ... (Transferencia UI se mantiene igual) ... */}
        {mode === 'transfer' && (
             // ... Código existente de transferencia ...
             <div className="grid grid-cols-2 gap-4 bg-zinc-900 p-3 rounded-lg">
                <div className="space-y-2">
                    <Label>Desde</Label>
                    <Select onValueChange={setAccountId} required>
                        <SelectTrigger className="bg-zinc-800 border-zinc-700"><SelectValue placeholder="Origen" /></SelectTrigger>
                        <SelectContent>{accounts.filter(a => !a.isHidden).map(acc => <SelectItem key={acc.id} value={acc.id}>{acc.name}</SelectItem>)}</SelectContent>
                    </Select>
                </div>
                <div className="space-y-2">
                    <Label>Hacia</Label>
                    <Select onValueChange={setTargetAccountId} required>
                        <SelectTrigger className="bg-zinc-800 border-zinc-700"><SelectValue placeholder="Destino" /></SelectTrigger>
                        <SelectContent>{accounts.filter(a => !a.isHidden && a.id !== accountId).map(acc => <SelectItem key={acc.id} value={acc.id}>{acc.name}</SelectItem>)}</SelectContent>
                    </Select>
                </div>
            </div>
        )}

        <div className="space-y-2">
            <Label>Descripción</Label>
            <Input className="bg-zinc-800 border-zinc-700" value={desc} onChange={e => setDesc(e.target.value)} placeholder="Descripción..." />
        </div>

        {/* --- OPCIÓN: CREAR NUEVO RECURRENTE (Si no elegí uno existente) --- */}
        {mode === 'expense' && !isFixedMode && (
            <div className="flex items-start gap-2 pt-2">
                <Checkbox 
                    id="create-rule" 
                    checked={createRuleFromTx} 
                    onCheckedChange={(c:any) => setCreateRuleFromTx(c)}
                    className="mt-1 border-zinc-500 data-[state=checked]:bg-indigo-600 data-[state=checked]:border-indigo-600"
                />
                <div className="grid gap-1.5 leading-none">
                    <label
                        htmlFor="create-rule"
                        className="text-sm font-medium leading-none peer-disabled:cursor-not-allowed peer-disabled:opacity-70 cursor-pointer"
                    >
                        Guardar como Gasto Recurrente
                    </label>
                    <p className="text-xs text-zinc-500">
                        Se creará una regla para recordarte este pago mensualmente.
                    </p>
                    
                    {createRuleFromTx && (
                        <div className="flex items-center gap-2 mt-2 animate-in fade-in">
                            <Label className="text-xs whitespace-nowrap">Día del mes:</Label>
                            <Input 
                                type="number" min="1" max="31" 
                                className="w-16 h-8 bg-zinc-900 border-zinc-700 text-center"
                                value={newRuleDay} onChange={e => setNewRuleDay(e.target.value)}
                            />
                        </div>
                    )}
                </div>
            </div>
        )}

        <Button type="submit" className={`w-full h-12 text-lg ${mode === 'income' ? 'bg-green-600' : mode === 'transfer' ? 'bg-blue-600' : 'bg-red-600'}`}>
          <Save className="mr-2 h-5 w-5" /> 
          {mode === 'income' ? 'Registrar Ingreso' : mode === 'transfer' ? 'Transferir' : 'Registrar Gasto'}
        </Button>
      </form>
    </div>
  );
}